@import '../../App.scss';

.page-container{
    @include w_h(100%, 100%);
    display: flex;
    background-color: $dark-gray;
    z-index: -2; 

    @media screen and (min-width : $screen_small) {
        padding-top: $nav_dimension
    }

    @media screen and (min-width : $screen_large) {
        padding-top: 0px;
    }

    &>.about, &>.skills, &>.gallery, &>.contact{
        animation: fadein 1s linear 0s 1;
        animation-fill-mode: backwards;

        max-width: 100%;
        flex-grow: 1;
        max-height: 100%;
        overflow-x: hidden;
        overflow-y: auto;

        
    }
}