@import '../../App.scss';

.about {
    display: flex; flex-direction: column;
    text-align: justify;

    @media screen and (min-width: $screen_small) {
        padding-top: 0%
    }
    @media screen and (min-width: $screen_large) {
        padding-top: 60px;
    }
    
    & .body{
        width: 100%;
        color : white;

        & p {
            font-size: calc(12px + 1vmin);
            @include setPropByBreakpointsCustom(padding, 
                (0px 50px 10px 50px)
                (0px 50px 15px 50px)
                (0px 50px 20px 50px)
                (0px 20px 20px 50px)
            );
        }
    }
}

.title {
    color : $primary-light;
    margin-top: 20px;
    margin-bottom: 0px;
    @include setPropByBreakpointsCustom(padding-left, 20px 20px 25px 30px);
    @include spannedTextJump();

    @media screen and (min-width: $screen_small){
        font-size: 35px;
    }
    @media screen and (min-width: $screen_medium){
        font-size: 45px;
    }
    @media screen and (min-width: $screen_large){
        font-size: 55px;
    }
    @media screen and (min-width: $screen_larger){
        font-size: 65px;
    }
}

.grid-wrapper{
    flex-grow: 1;
    overflow-y: auto;
}