@import '../../App.scss';
.grid{
    display: grid;
    max-height: 100%; height: 100%;
    
    @media screen and (min-width: $screen_small) {
        grid-template-columns: 100% ;
        overflow-y: auto;
           
    }
    @media screen and (min-width: $screen_larger) {
        grid-template-columns: 50% auto; 
        overflow-y: hidden;
        & div {
            max-height: 100%;
            overflow-y: auto;
        }
    }

    
}